html {
  scroll-behavior: smooth;
}
body {
  color: $main-text-color;
  background-color: $based-color;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0;
}

h1,
h2,
h3,
p,
section {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
