.features .container {
        @media screen and (min-width:768px) {
            display: flex;
            flex-direction: column;
        }
        @media screen and (min-width:1200px) {
            flex-direction: row-reverse;
            justify-content: space-around;
        }
}

@media screen and (min-width:768px) {   
.features-first {
    width: 500px;
}

.features-second {
    width: 450px;
    margin-left: 250px;

    @media screen and (min-width:1200px) {
        width: 370px;
        margin-left: 0;
    }
}
}

.features__title {
    margin-bottom: 20px;
}

.features__text {
    margin-bottom: 44px;
    @include font (null, 18px, 1.67, 0.02em);
    color: $article-text-color;
}

.features__list {
    display: flex;
    flex-wrap: wrap;
}

.features__item {
    width: 193px;
    height: 133px;
    padding: auto;
    @extend %flex-centered;
    flex-direction: column;
    background-color: $secondary-based-color;

    span {
        @include font (700, 42px, 1.36, 0.05em);
        text-align: center;
    
        sup {
            margin-left: 2px;
            @include font (600, 16px, 1.38, null);
            color: $accent-color;
        }
    }
    p {
        @include font (600, 11px, 1.36, null);
            padding-left: 10px;
            padding-right: 10px;
            text-align: center;
    }

@media screen and (max-width:479px) {
    width: calc((100% - 2 * 15px) / 2);
}

@media screen and (min-width: 1200px) {
    width: 170px;
    height: 133px;
}
}

.features__item:nth-child(2n+1) {
    margin-right: 30px;
  }

.features__item:not(:last-child) {
    margin-bottom: 15px;
  }

.features__item:nth-child(2n) {
    margin-top: 30px;
  }

  .features__item:nth-child(2n) {
    margin-bottom: 0;
  }
  